* {
    font-family: 'Roboto', sans-serif;
    color: #F5F5F5;
}

body {
    background-color: #121212;
}

::-webkit-scrollbar {
    width: 8px;
    height: 0;
}

 ::-webkit-scrollbar-track {
    background: none;
}

 ::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 2px;
}

 ::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.5);
    background: #555;
}

.App {
    text-align: center;
}

nav {
    top: 0;
    left: 0;
    display: flex;
    height: 64px;
    align-items: center;
    justify-content: space-around;
    background-color: #121212;
    border-bottom: 1px solid #A3E4DB;
}

.nav-spacer {
    flex-grow: 0.8;
}

nav a {
    text-decoration: none;
    font-size: 26px;
    font-weight: 600;
    padding: 14px;
    border-radius: 4px;
    cursor: pointer;
    transition: 100ms ease-out;
}

nav a:hover {
    background-color: white;
    color: #115a50;
    font-size: 28px;
}

hr {
    border: 1px solid #A3E4DB;
    background-color: #A3E4DB;
    margin: 0 auto;
    width: 15%;
}

#main-container {
    height: calc(100vh - 65px);
    overflow: scroll;
}

h1 {
    color: #A3E4DB;
}

.home-title {
    margin-top: 10vh;
    margin-left: 48px;
    text-align: start;
    font-size: 18vh;
    font-weight: 600;
    color: #A3E4DB;
    line-height: 18vh;
}

.home-subtitle {
    font-family: 'Roboto Mono', monospace;
    margin-left: 48px;
    text-align: start;
    font-size: 6vh;
}

.home-about-me {
    margin-top: 15vh;
    padding: 8vh 0;
    background-color: #A3E4DB;
    padding-bottom: 0;
}

.home-about-me h1 {
    font-size: 2.5vh;
    margin-top: 0;
    color: black;
}

.home-about-me hr {
    border: 1px solid black;
    background-color: black;
}

.home-about-me div {
    color: black;
    font-size: 20px;
    font-size: 1.6vh;
}

.home-about-me img {
    margin-top: 32px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.resume iframe {
    border: none;
    border-radius: 2px;
}

.resume-div {
    margin: 0 30vw;
    padding: 8px;
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, 0.5) 0 4px 4px 4px;
}

.resume-div p,
.resume-div h2,
.resume-div h3 {
    text-align: start;
    margin: 8px;
}

.portfolio .video-container {
    display: grid;
    grid-template-columns: calc(50% - 16px) calc(50% - 16px);
    column-gap: 32px;
    margin: 32px 32px 8px;
}

.portfolio .container {
    display: grid;
    grid-template-columns: auto auto auto auto auto auto;
    margin: 0 16px;
}

.portfolio video {
    width: 100%;
    border-radius: 4px;
}

.portfolio img {
    width: 100%;
    border-radius: 4px;
}

.portfolio .item {
    margin: 16px;
    overflow: hidden;
}

.portfolio h2 {
    margin-bottom: 0;
}

.projects .container {
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    margin: 0 16px;
}

.projects .single-item {
    margin: 0 16px;
}

.projects .item {
    background-color: rgba(0, 0, 0, 0.8);
    margin: 16px;
    border-radius: 4px;
    font-size: 30px;
    text-align: center;
}

.projects .single-item .item {
    display: flex;
}

.projects .single-item .info-box {
    width: 50%;
}

.projects .item .title {
    margin: 16px 0;
    font-size: 32px;
}

.projects .item hr {
    background-color: rgba(255, 255, 255, 0.4);
    border: 1px solid rgba(255, 255, 255, 0.4);
    width: 50%;
}

.projects .item .description {
    padding: 8px;
    font-size: 18px;
    text-align: start;
    line-height: 26px;
    height: 150px;
    overflow: scroll;
}

.projects .single-item .item .description {
    height: 360px;
    font-size: 26px;
}

.projects .item .extra-info {
    color: rgba(255, 255, 255, 0.7);
    padding: 8px;
    font-size: 18px;
}

.projects .item img {
    width: 28px;
    vertical-align: bottom;
    margin-right: 8px;
}

.video-responsive {
    height: 200px;
}

.video-responsive-big {
    height: 500px;
    width: 80%;
}

.video-responsive iframe,
.video-responsive-big iframe {
    height: 100%;
    width: 100%;
}

@media only screen and (max-width: 1600px) {
    .projects .container {
        grid-template-columns: 33.3% 33.3% 33.3%;
    }
    .portfolio .container {
        grid-template-columns: auto auto auto auto auto;
    }
}

@media only screen and (max-width: 1200px) {
    .projects .container {
        grid-template-columns: 50% 50%;
    }
    .portfolio .container {
        grid-template-columns: auto auto auto auto;
    }
    .projects .single-item .item {
        flex-direction: column;
    }
    .projects .single-item .item .description {
        height: 250px;
        font-size: 20px;
    }
    .video-responsive-big {
        height: 300px;
        width: 100%;
    }
    .projects .single-item .info-box {
        width: 100%;
    }
}

@media only screen and (max-width: 800px) {
    .projects .container {
        grid-template-columns: 100%;
    }
    .portfolio .container {
        grid-template-columns: auto auto;
    }
    .home-title {
        margin-top: 32px;
        margin-left: 32px;
        font-size: 15vw;
    }
    .home-subtitle {
        margin-left: 32px;
        font-size: 5vw;
    }
    nav a {
        font-size: 4vw;
    }
    nav a:hover {
        font-size: 4vw;
    }
    .portfolio .video-container {
        grid-template-columns: auto;
    }
    @media only screen and (max-height: 600px) {
        .home-title {
            font-size: 15vh;
        }
        .home-subtitle {
            font-size: 5vh;
        }
        .projects .container {
            grid-template-columns: 50% 50%;
        }
        .portfolio .container {
            grid-template-columns: auto auto;
        }
    }
}